"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { Home, Tag, User } from "lucide-react";

export default function Navigation() {
  const pathname = usePathname();

  const links = [
    { href: "/", label: "الرئيسية", icon: Home },
    { href: "/offers", label: "العروض", icon: Tag },
    { href: "/profile", label: "الملف الشخصي", icon: User },
  ];

  return (
    <>
      {/* Top Navbar for Desktop */}
      <nav className="hidden md:flex w-full bg-white border-b fixed top-0 left-0 z-50">
        <div className="max-w-6xl mx-auto flex justify-between items-center w-full p-4">
          <div className="text-lg font-bold">Logo</div>
          <div className="flex gap-6 text-gray-700">
            {links.map(({ href, label }) => (
              <Link
                key={href}
                href={href}
                className={`hover:text-green-600 ${
                  pathname === href ? "text-green-600 font-medium" : ""
                }`}
              >
                {label}
              </Link>
            ))}
          </div>
        </div>
      </nav>

      {/* Bottom Navbar for Mobile */}
      <div className="flex md:hidden fixed bottom-0 left-0 right-0 bg-white border-t justify-around py-2 z-50">
        {links.map(({ href, label, icon: Icon }) => (
          <Link
            key={href}
            href={href}
            className={`flex flex-col items-center ${
              pathname === href ? "text-green-600" : "text-gray-500"
            }`}
          >
            <Icon className="w-6 h-6" />
            <span className="text-xs">{label}</span>
          </Link>
        ))}
      </div>
    </>
  );
}
